.cards-list {
  display: grid;
  grid-template-columns: 100%;
  padding-bottom: 100px;
  
  @media (min-width: $lg) {
    grid-template-columns: 48% 48%;
    grid-column-gap: 20px;
    -ms-grid-column-gap: 20px;
  }

  @media (min-width: $xl) {
    grid-template-columns: 32% 32% 32%;
  }
}